<template>
  <div class="container">
    <section class="registered">
      <h2 class="title"></h2>
      <div class="tips" :class="{'close': tips}">
        <div class="icon"></div>
        <div class="cancel" @click="cancleTips()"></div>
        <p class="text"></p>
      </div>
      <form class="registered-form" autocomplete="off" @submit.prevent="registered">
        <label class="label-item" for="account"></label>
        <input
          type="text"
          class="input-item"
          id="account"
          v-model="account"
          required
          v-focus
        />

        <label class="label-item" for="password"></label>
        <input type="password" class="input-item" id="password" v-model="password" required />

        <label class="label-item" for="password-again"></label>
        <input
          type="password"
          class="input-item"
          id="password-again"
          v-model="passwordAgain"
          required
        />

        <label class="label-item" for="email"></label>
        <input type="email" class="input-item" id="email" v-model="email" required />

        <label class="label-item" for="username"></label>
        <input
          type="text"
          class="input-item"
          id="username"
          v-model="username"
          required
        />

        <button class="submit"></button>
      </form>
    </section>
  </div>
</template>

<style src="../../assets/login/styles/login.css" scoped></style>

<script>
export default {
  data() {
    return {
      account: "",
      password: "",
      passwordAgain: "",
      email: "",
      username: "",
      tips: false
    };
  },
  methods: {
    registered() {

      if (this.password !== this.passwordAgain) {
        alert("!");
        this.password = "";
        this.passwordAgain = "";
        return;
      }

      let newMemberData = {
        id: this.$store.getters.getId,
        account: this.account,
        password: this.password,
        email: this.email,
        username: this.username
      };

      for (const item of this.$store.getters.getMemberData) {
        if (item.account === this.account) {
          alert("!");
          this.account = "";
          return;
        }

        if (item.email === this.email) {
          alert("!");
          this.email = "";
          return;
        }
      }

      this.$store.commit("setMemberData", newMemberData);
      alert("!");
      this.$router.push({ name: "index" });
    },
    cancleTips() {
      this.tips = true;
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  }
};
</script>