import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const filter = {
  all(productData) {
    return productData
  },
  flowers(productData) {
    return productData.filter(item => {
      return item.type === 'flower'
    })
  },
  seed(productData) {
    return productData.filter(item => {
      return item.type === 'seed'
    })
  },
  material(productData) {
    return productData.filter(item => {
      return item.type === 'material'
    })
  }
}

