<template>
  <div class="container">
    <section class="connection">
      <h2 class="title"></h2>
      <!-- TODO , , , Email, , ,  -->
      <form
        class="service-form"
        action="#"
        method="get"
        @submit.prevent="send()"
        autocomplete="off"
      >
        <div class="row">
          <label class="f-title" for="name"></label>
          <input
            class="f-content"
            type="text"
            name="name"
            id="name"
            spellcheck="false"
            required
            v-focus
          />
        </div>
        <div class="row">
          <p class="f-title"></p>
          <div class="gender-group">
            <input class="gender-input" type="radio" name="gender" id="male" checked />
            <label class="gender-text" for="male"></label>
            <input class="gender-input" type="radio" name="gender" id="female" />
            <label class="gender-text" for="female"></label>
          </div>
        </div>
        <div class="row">
          <label class="f-title" for="contact"></label>
          <input
            class="f-content"
            type="tel"
            name="number"
            id="contact"
            spellcheck="false"
            required
          />
        </div>
        <div class="row">
          <label class="f-title" for="email">Email</label>
          <input class="f-content" type="email" name="email" id="email" spellcheck="false" required />
        </div>
        <div class="row">
          <label class="f-title" for="issue"></label>
          <select class="f-content" name="issue" id="issue" required>
            <option value></option>
            <option value="wedding"></option>
            <option value="gardening"></option>
            <option value="farming"></option>
            <option value="opinion"></option>
            <option value="other"></option>
          </select>
        </div>
        <div class="row">
          <label class="f-title" for="text"></label>
          <textarea class="f-content" name="text" id="text" cols="60" rows="7" spellcheck="false"></textarea>
        </div>
        <div class="row">
          <button class="submit-btn"></button>
        </div>
      </form>
    </section>
    <section class="map">
      <h2 class="title"></h2>
      <div class="google-map">
        <GmapMap
          class="google-map-item"
          :center="{ lat:22.635027, lng:120.335638 }"
          :zoom="12"
          map-type-id="terrain"
          :style="mapStyle"
        >
          <GmapMarker
            :position="{ lat:22.635027, lng:120.335638 }"
            :clickable="true"
            :draggable="true"
          />
        </GmapMap>
      </div>
    </section>
  </div>
</template>

<style src="../assets/service/styles/service.css" scoped></style>

<script>
export default {
  computed: {
    mapStyle() {
      return {
        width: 700 + "px",
        height: 450 + "px",
        marginLeft: 50 + "px"
      };
    }
  },
  methods: {
    send() {
      alert("");
      this.$router.push({ name: "index" })
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  }
};
</script>